@import "variables.scss";

@font-face {
  font-family: 'GothamPro';
  src: url('../fonts/GothamPro.eot');
  src: url('../fonts/GothamPro.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GothamPro.woff') format('woff'),
  url('../fonts/GothamPro.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'GothamPro';
  src: url('../fonts/GothamPro-Medium.eot');
  src: url('../fonts/GothamPro-Medium.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GothamPro-Medium.woff') format('woff'),
  url('../fonts/GothamPro-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'GothamPro';
  src: url('../fonts/GothamPro-Black.eot');
  src: url('../fonts/GothamPro-Black.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GothamPro-Black.woff') format('woff'),
  url('../fonts/GothamPro-Black.ttf') format('truetype');
  font-weight: 900;
  font-style: normal;
}


* {
  margin: 0;
  padding: 0;
}


body {
  min-width: 320px;

  font-family: $font-family;

  &.overfl {
    overflow: hidden;
    height: 100vh;
  }
}

.start-page {
  padding: 30px;

  &-logo {
    display: block;
    margin-bottom: 30px;
  }

  ul {
    display: inline-block;
    vertical-align: top;
    padding-right: 60px;

    li {
      padding: 5px 0;

      b {
        font-size: 20px;
      }

      a {
        font-size: 16px;
        line-height: 140%;
        color: $black;

        &:hover {
          //color: $active;
        }
      }
    }
  }
}

div,
span,
a,
input,
textarea,
select,
button,
.button {
  &:focus {
    outline: none;
  }
}

input,
textarea {
  box-shadow: none!important;
}


ul {
  margin: 0;
  padding: 0;

  li {
    list-style: none;
  }
}

a {
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

.container {
  max-width: 100%;

  @media screen and(min-width: 1220px) {
    max-width: 1190px;
  }
}

.btn-close:focus {
  box-shadow: none!important;
}

.button {
  border: none;
  box-shadow: none;

  text-align: center;
  outline: none!important;

  &-primary {
    min-width: 285px;
    display: inline-block;
    padding: 19px 40px;

    position: relative;

    border-radius: 29px;
    //overflow: hidden;

    transition: 0.3s;

    &:before {
      content: '';
      width: 100%;
      height: 100%;
      display: block;

      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      background: rgba(149,202,251,1);
      background: -moz-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
      background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(149,202,251,1)), color-stop(100%, rgba(44,116,183,1)));
      background: -webkit-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
      background: -o-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
      background: -ms-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
      background: linear-gradient(to bottom, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#95cafb', endColorstr='#2c74b7', GradientType=0 );
      transition: 0.3s;

      border-radius: 50px;
      overflow: hidden;
    }

    span {
      position: relative;
      z-index: 3;

      color: $white;
      font-size: 16px;
      line-height: 24px;
    }

    &:hover {
      &:before {
        background: rgba(44,116,183,1);
        background: -moz-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
        background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(44,116,183,1)), color-stop(100%, rgba(149,202,251,1)));
        background: -webkit-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
        background: -o-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
        background: -ms-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
        background: linear-gradient(to bottom, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2c74b7', endColorstr='#95cafb', GradientType=0 );
      }
    }
  }

  &-bord {
    display: inline-block;
    height: 58px;
    min-width: 270px;
    padding: 3px;

    text-align: center;
    border-radius: 29px;
    //overflow: hidden;

    transition: 0.3s;

    background: rgba(149,202,251,1);
    background: -moz-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(149,202,251,1)), color-stop(100%, rgba(44,116,183,1)));
    background: -webkit-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    background: -o-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    background: -ms-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    background: linear-gradient(to bottom, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#95cafb', endColorstr='#2c74b7', GradientType=0 );

    span {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      position: relative;
      z-index: 3;

      color: $blue3;
      font-size: 16px;
      line-height: 20px;
      background: $white;
      border-radius: 29px;
    }

    &:hover {
      background: rgba(44,116,183,1);
      background: -moz-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(44,116,183,1)), color-stop(100%, rgba(149,202,251,1)));
      background: -webkit-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -o-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -ms-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: linear-gradient(to bottom, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2c74b7', endColorstr='#95cafb', GradientType=0 );
    }
  }

  &-big {
    padding: 25px 84px;
    border-radius: 50px;
  }

  &-bigT {
    padding: 17px 84px;
  }

  &-center {
    width: 100%;
    text-align: center;
  }

  &-call {
    &:after {
      content: '';
      width: 36px;
      height: 45px;
      display: block;
      position: absolute;
      top: -25px;
      left: 0;
      right: 0;
      z-index: 2;
      margin: auto;
      background-image: url(../images/phone.png);
      background-repeat: no-repeat;
    }
  }

  &-dop {
    display: inline-block;

    &-title {
      color: $blue3;
    }

    &-tx {
      margin-top: 20px;
      margin-left: 350px;

      position: relative;

      font-size: 16px;
      line-height: 20px;

      color: $gray;

      .action & {
        margin-top: 35px;
      }

      &:before {
        content: '';
        width: 71px;
        height: 40px;
        display: block;
        position: absolute;
        top: -11px;
        left: -77px;
        z-index: 1;
        background-image: url(../images/arrow-btn.png);
        background-repeat: no-repeat;
      }
    }
  }

}

h1 {
  margin-bottom: 14px;

  font-weight: 900;
  font-size: 38px;
  line-height: 50px;
  color: $gray;
}

h2 {
  margin-bottom: 25px;

  font-weight: 900;
  font-size: 34px;
  line-height: 38px;
  color: $gray;
  text-align: center;
}

.header {
  &-mob {
    display: none;
  }

  &__close {
    display: none;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__left {
    display: flex;
  }

  &__right {
    &.tw {
      display: none;
    }
  }

  &__logo {
    height: 94px;

    padding: 20px 25px;
    display: inline-block;

    position: relative;
    z-index: 2;

    background: rgba(230,230,230,1);
    background: -moz-linear-gradient(left, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    background: -webkit-gradient(left top, right top, color-stop(0%, rgba(230,230,230,1)), color-stop(100%, rgba(250,250,250,1)));
    background: -webkit-linear-gradient(left, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    background: -o-linear-gradient(left, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    background: -ms-linear-gradient(left, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    background: linear-gradient(to right, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e6e6e6', endColorstr='#fafafa', GradientType=1 );

    box-shadow: 2px 3px 4px rgba(0,0,0,0.11);
    border-radius: 0 0 8px 8px;
  }

  &__tx {
    height: 94px;
    padding: 17px 55px 17px 33px;
    display: flex;
    align-items: center;

    position: relative;
    z-index: 1;
    left: -7px;

    font-size: 14px;
    line-height: 16px;
    color: $gray;

    background: rgba(230,230,230,1);
    background: -moz-linear-gradient(left, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    background: -webkit-gradient(left top, right top, color-stop(0%, rgba(230,230,230,1)), color-stop(100%, rgba(250,250,250,1)));
    background: -webkit-linear-gradient(left, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    background: -o-linear-gradient(left, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    background: -ms-linear-gradient(left, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    background: linear-gradient(to right, rgba(230,230,230,1) 0%, rgba(250,250,250,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e6e6e6', endColorstr='#fafafa', GradientType=1 );

    box-shadow: 2px 3px 4px rgba(0,0,0,0.11);
    border-radius: 0 8px 8px 0;
  }

  &__info {
    margin-right: 70px;
    display: inline-block;
    vertical-align: middle;

    &-tx {
      color: $gray;
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__tel {
    display: inline-block;
    margin-bottom: 2px;

    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: $blue;
    letter-spacing: 0.1em;

    &:hover {
      color: $blue2;
    }
  }

  .button {
    display: inline-block;
    vertical-align: middle;
  }

  &__menu {
    padding: 20px 0;

    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;

      li {
        position: relative;

        span {
          display: inline-block;

          position: relative;

          color: $gray;
          font-size: 18px;
          line-height: 24px;
          cursor: pointer;

          &:after {
            content: '';
            width: 100%;
            height: 2px;
            display: block;
            opacity: 0;

            position: absolute;
            bottom: 0;
            left: 0;
            z-index: 1;

            background: rgba(149,202,251,1);
            background: -moz-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(149,202,251,1)), color-stop(100%, rgba(44,116,183,1)));
            background: -webkit-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            background: -o-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            background: -ms-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            background: linear-gradient(to bottom, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#95cafb', endColorstr='#2c74b7', GradientType=0 );
          }
        }

        a {
          display: inline-block;

          position: relative;

          color: $gray;
          font-size: 18px;
          line-height: 24px;

          &:after {
            content: '';
            width: 100%;
            height: 2px;
            display: block;
            opacity: 0;

            position: absolute;
            bottom: 0;
            left: 0;
            z-index: 1;

            background: rgba(149,202,251,1);
            background: -moz-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(149,202,251,1)), color-stop(100%, rgba(44,116,183,1)));
            background: -webkit-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            background: -o-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            background: -ms-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            background: linear-gradient(to bottom, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#95cafb', endColorstr='#2c74b7', GradientType=0 );
          }

          &:hover {
            color: $blue;
          }
        }

        &.active{
          a, span {
            &:after {
              opacity: 1;
            }
          }
        }

        &:hover {
         & >  ul {
            display: block;
          }
        }

        ul {
          display: none;
          padding: 27px 30px;
          width: 700px;

          position: absolute;
          top: 33px;
          left: -30px;
          z-index: 5;

          background: $white;
          border-radius: 5px;
          box-shadow: 9px 0 27px rgba(0,0,0,0.1);

          &:before {
            content: '';
            width: 100%;
            height: 20px;
            display: block;

            position: absolute;
            top: -10px;
            left: 0;

            background: transparent;
          }

          li {
            margin-bottom: 16px;

            &:last-of-type {
              margin-bottom: 0;
            }

            a {
              &:after {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}

.banner {
  //margin-bottom: 200px;
  padding: 100px 0;
  height: calc(100vh - 160px);
  min-height: 750px;

  position: relative;

  &-o {
    position: relative;

    //overflow: hidden;

    @media screen and(min-width: 991px){
      .banner {
        padding: 20px 0 60px;

        &__lt {
          margin-top: 10px;
        }

        &__bts {
          margin-top: 15px;
        }

        &__list {
          margin-top: 15px;
        }

        &__btn {
          margin-top: 25px;
        }
      }
    }

    &.tw {
      .banner {
        margin-bottom: 80px;
      }
    }
  }

  &-ov {
    position: relative;

    overflow: hidden;

    &:before {
      content: '';
      width: 100%;
      height: 100%;
      display: block;

      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      background-image: url("../images/banner-bg.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: top center;
    }

  }

  &__inner {
    display: flex;

    position: relative;
    z-index: 2;
  }

  &__img {
    width: 35%;

    position: absolute;
    left: 0;
    top: -85px;
    z-index: 2;

    .vd & {
      width: 50%;
    }

    .tw & {
      position: relative;
      top: 0;
    }

    &.th {
      margin-top: -350px;

      img {
        position: relative;
        right: 100px;
      }
    }
  }

  &__vd {
    width: 100%;
    height: 335px;

    position: relative;
    border-radius: 29px;
    overflow: hidden;

    .embed-responsive, iframe {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    width: 75%;

    position: relative;
    z-index: 3;

    .vd & {
      width: 50%;
    }

    .banner__vd {
      display: none;
    }

    &-big {
      margin-bottom: 21px;

      font-size: 38px;
      line-height: 48px;
      font-weight: 500;
      color: $gray;
    }

    &-tx {
      font-weight: 500;
      font-size: 22px;
      line-height: 30px;
      color: $gray;

      b {
        font-weight: 900;
      }

      span {
        color: $blue3;
      }

      &.tt {
        margin-bottom: 60px;

        b {
          font-weight: 500;
        }

        @media screen and (min-width: 991px){
          font-size: 24px;
          line-height: 30px;
        }
      }
    }
  }

  &__list {
    display: flex;
    margin-top: 45px;

    &-item {
      width: 33.333%;
      padding-right: 45px;

      position: relative;

      &:before {
        content: '';
        width: 69px;
        height: 69px;
        display: block;
        margin-left: -11px;

        background-image: url("../images/check.png");
        background-repeat: no-repeat;
        background-position: center;
      }
    }

    &-title {
      margin-bottom: 10px;

      font-weight: 900;
      font-size: 22px;
      line-height: 30px;
      color: $blue;
    }

    &-tx {
      font-size: 18px;
      line-height: 24px;
      color: $gray;
    }
  }

  &__lt {
    margin: 35px 0 20px;

    li {
      margin-bottom: 10px;
      padding-left: 35px;
      position: relative;

      font-size: 18px;
      line-height: 26px;
      color: $gray;
      font-weight: 500;

      &:before {
        content: '';
        width: 25px;
        height: 25px;
        display: block;
        position: absolute;
        top: 2px;
        left: 0;
        z-index: 3;
        background-image: url(../images/arrow.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        -webkit-transform: rotate( -90deg);
        transform: rotate(  -90deg);
      }
    }
  }

  &__btn {
    margin-top: 70px;

    position: relative;

    .button {
      min-width: 280px;
    }

    &-dop {
      width: 210px;
      margin-top: 12px;
      margin-left: 330px;

      position: absolute;
      top: 10px;

      text-align: center;

      &:before {
        content: '';
        width: 71px;
        height: 39px;
        display: block;

        position: absolute;
        top: 42px;
        left: -72px;
        z-index: 1;

        background-image: url("../images/arrow-btn.png");
        background-repeat: no-repeat;
      }

      &-title {
        font-size: 16px;
        line-height: 20px;
        color: $blue;
        font-weight: 500;
      }

      &-tx {

        font-size: 16px;
        line-height: 20px;
        color: $gray;
      }
    }
  }

  &__mouse {
    width: 19px;
    height: 31px;

    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 3;

    &:before {
      content: '';
      height: 30px;
      width: 1px;
      display: block;

      position: absolute;
      top: -35px;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 2;

      background: $gray2;
    }

    &:after {
      content: '';
      height: 30px;
      width: 2px;
      display: block;

      position: absolute;
      bottom: -35px;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 2;

      border-right: 1px dotted $gray2;
    }
  }

  &__bts {
    margin-top: 25px;
    display: inline-block;


    span {
      display: block;
      margin: 3px;
      padding: 0;

      position: relative;
      z-index: 2;

      background: $white;
      font-weight: 700;
      color: $blue;
      font-size: 24px;
      line-height: 30px;

      border-radius: 30px;
    }

    &:hover {
      background: rgba(44,116,183,1);
      background: -moz-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(44,116,183,1)), color-stop(100%, rgba(149,202,251,1)));
      background: -webkit-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -o-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -ms-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: linear-gradient(to bottom, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2c74b7', endColorstr='#95cafb', GradientType=0 );
    }
  }
}

.video {
  padding: 65px 0 50px;

  background: $white2;
  box-shadow: 5px 10px 13px rgba(0,0,0,0.09);

  &-about {
    padding: 80px 0 0;

    background: transparent;
    box-shadow: none;

    &__inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__text {
      width: calc(100% - 580px);
      padding-right: 50px;

      h1 {
        margin-bottom: 25px;
      }

      p {
        margin-bottom: 10px;

        font-size: 18px;
        line-height: 27px;
        color: $gray2;
      }
