.slick {
  &-arrow {
    display: inline-block;
    width: 45px;
    height: 45px;

    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 2;

    border-radius: 100px;
    background: rgba(149,202,251,1);
    background: -moz-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(149,202,251,1)), color-stop(100%, rgba(44,116,183,1)));
    background: -webkit-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    background: -o-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    background: -ms-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    background: linear-gradient(to bottom, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#95cafb', endColorstr='#2c74b7', GradientType=0 );

    transform: none;
    cursor: pointer;

    &:before {
      content: '';
      display: block;
      width: 9px;
      height: 17px;

      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      z-index: 3;

      background-image: url("../images/arrow2.png");
      background-repeat: no-repeat;
      background-position: center;
    }

    &:hover {
      background: rgba(44,116,183,1);
      background: -moz-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(44,116,183,1)), color-stop(100%, rgba(149,202,251,1)));
      background: -webkit-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -o-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: -ms-linear-gradient(top, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      background: linear-gradient(to bottom, rgba(44,116,183,1) 0%, rgba(149,202,251,1) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2c74b7', endColorstr='#95cafb', GradientType=0 );
    }
  }

  &-prev {
    left: 0px;
  }

  &-next {
    right: 0px;

    &:before {
      transform: rotate(180deg);
    }
  }

  &-dots {
    width: 100%;
    padding: 0;
    margin: 20px 0 0;

    text-align: center;

    li {
      display: inline-block;
      margin: 0 5px;
      width: 12px;
      height: 12px;

      border: 1px solid $blue;
      background: $white;
      border-radius: 30px;

      button {
        display: none;
      }

      &.slick-active {
        background: rgba(149,202,251,1);
        background: -moz-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
        background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(149,202,251,1)), color-stop(100%, rgba(44,116,183,1)));
        background: -webkit-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
        background: -o-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
        background: -ms-linear-gradient(top, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
        background: linear-gradient(to bottom, rgba(149,202,251,1) 0%, rgba(44,116,183,1) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#95cafb', endColorstr='#2c74b7', GradientType=0 );
      }
    }
  }
}

