input {
  &::-webkit-input-placeholder { color: $white; }
  &:-ms-input-placeholder { color: $white; }
  &::placeholder { color: $white; }

  &[type="checkbox"] {
    display: none;

    & + label {
      padding-left: 18px;

      position: relative;

      font-size: 12px;
      line-height: 130%;
      cursor: pointer;

      &:before {
        content: '';
        width: 11px;
        height: 11px;
        display: block;

        position: absolute;
        top: 1px;
        left: 0;
        z-index: 2;

        border-radius: 1px;
      }

    }

    &:checked {
      & + label {
        &:before {
          background-image: url("../images/svg/check2.svg");
          background-repeat: no-repeat;
          background-size: 6px 7px;
          background-position: center;
          background-position: center;
        }
      }
    }
  }
}

